@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
:root {
  --input-shadow: 0 1px 5px 0 #00000026, 0 4px 4px 0 #0000001a, 0 -.1px 3px 0 #00000014;
  --default-box-shadow: 0 2px 6px 0 #0000001f, inset 0 -1px 0 0 #dadce0;
  --main-bg-color: #fff;
  --main-highlight: #8ae9c1;
  --main-highlight-selected: #a0eec0;
  --text-dark: #212121;
  --text-light: #fff;
  height: 100vh;
}

* {
  color: var(--text-dark);
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  width: inherit;
  max-height: 100vh;
  background-color: var(--main-bg-color);
  flex-direction: column;
  display: flex;
}

header {
  background-color: var(--main-highlight);
  box-shadow: var(--default-box-shadow);
  height: 20%;
  justify-content: space-between;
  padding: 10px 15px;
  display: flex;
}

footer {
  background-color: var(--main-highlight);
}

main {
  box-sizing: border-box;
  width: inherit;
  height: inherit;
  flex-direction: column;
  margin-top: 50px;
  display: flex;
  overflow-y: hidden;
}

#website-title {
  font-size: 18px;
}

#dark-mode-btn {
  margin-right: 10px;
}

#aliza-submit-text {
  width: 100%;
  height: 42px;
  max-width: 584px;
  box-shadow: var(--default-box-shadow);
  border: none;
  border-radius: 26px;
  padding: 3px 30px;
}

#aliza-portrait-canvas {
  width: 20%;
  max-width: 147.75px;
  min-width: 118.233px;
}

.webpage-title {
  font-weight: 900;
}

.aliza-portrait-container {
  justify-content: center;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.speech-bubble-container {
  width: 30%;
  align-items: center;
  margin-left: 5px;
  display: flex;
}

.speech-bubble {
  box-sizing: border-box;
  background: var(--main-highlight);
  color: var(--text-dark);
  box-shadow: var(--default-box-shadow);
  width: 100%;
  border-radius: 8px;
  margin-bottom: 5px;
  padding: 20px 0 0 20px;
  font-weight: 900;
}

.speech-bubble-arrow {
  width: 0;
  height: 0;
  border: 20px solid #0000;
  border-right-color: var(--main-highlight);
  border-bottom: 0;
  border-left: 0;
  position: relative;
}

.input-field-container {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  margin: 0 50px;
  display: flex;
}

.input-field-message {
  min-height: 30px;
  width: 100%;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.submit-btn, .submit-btn:hover {
  min-width: 68px;
  min-height: 48px;
  box-shadow: var(--default-box-shadow);
  background-color: var(--main-highlight);
  border: none;
  border-radius: 26px;
  margin-left: 10px;
  padding: 10px;
  font-weight: 900;
}

.submit-btn:hover {
  background-color: var(--main-highlight-selected);
}

.submit-btn:active {
  transform: translateY(1px);
}

.transcript-container {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
  overflow-y: hidden;
}

.transcript {
  resize: none;
  scrollbar-width: thin;
  max-width: 690px;
  width: 100%;
  justify-content: center;
  overflow-y: auto;
}

.transcript-element {
  display: flex;
}

.transcript-speaker-name {
  color: #00f;
}

.transcript-speaker-text {
  color: red;
  margin-left: 10px;
}

